import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import ReactGA from 'react-ga';
import {Helmet} from "react-helmet";
// import MetaTagComponent from './components/MetaTags';

ReactGA.initialize('UA-162326038-1');
ReactGA.pageview(window.location.pathname + window.location.search);
ReactDOM.render(
  <React.StrictMode>
    {/* <MetaTagComponent/> */}
    <Helmet>
    <meta charSet="utf-8" />
      <title>Covid-19 Updates from India :: Suraj Kamdi</title>
      <meta name="description" content="Covid-19 (Corona) Live Updates From India. 
      WebApp is Designed and Developed by Suraj Kamdi Web site created using create-react-app"/>
      <link rel="canonical" href="https://surajkamdi.github.io/covid-19-updates-webapp/" />
    </Helmet>
    <App/>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
